<template>
  <div id="app">
    <router-view/>
    <banner v-show="showBanner"></banner>
  </div>
</template>

<script>
  import Banner from '@/components/Banner.vue'
  import {mapState} from 'vuex'

  export default {
    name: 'App',
    components: {
      Banner
    },
    computed: {
      ...mapState(['showBanner'])
    }
  }
</script>

<style>
  #app {
    font-family: sans-serif;
  }
</style>



