<template>
  <div class="home-header">
    <div class="header-tab">
      <div class="header-shortessay" :class="{active:showHomeEssay}" @click="handleEssayClick">短文</div>
      <div class="header-book" :class="{active:!showHomeEssay}" @click="handleBookClick">书籍</div>
    </div>
    <div class="header-remind iconfont">&#xe634;</div>
  </div>
</template>

<script>
  import {mapState, mapMutations} from 'vuex';

  export default {
    name: "homeHeader",
    methods: {
      ...mapMutations(['UPDATE_SHOWESSAY']),
      handleEssayClick() {
        this.UPDATE_SHOWESSAY(1)
      },
      handleBookClick() {
        this.UPDATE_SHOWESSAY(0)
      }
    },
    computed:{
      ...mapState(['showHomeEssay'])
    }
  }
</script>

<style lang="less" scoped>
  .home-header {
    z-index: 99;
    position: fixed;
    width: 100%;
    height: .5rem;
    line-height: .5rem;
    background-color: #fff;
    /*background-color: #25a4bb;*/
    .header-tab {
      width: 1rem;
      height: .3rem;
      line-height: .3rem;
      /*background-color: red;*/
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: .2rem;
      color: #999;

      .header-shortessay {
        float: left;
      }

      .header-book {
        float: right;
      }

      .active {
        font-size: .22rem;
        color: #333;
        border-bottom: .02rem solid #209e85;
      }
    }

    .header-remind {
      position: absolute;
      top: 0;
      right: .1rem;
      font-size: .2rem;
      color: #666;
    }

  }
</style>



