import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state = {
  showBanner: false,
  showHomeEssay:1
}

const mutations = {
  CHANGE_BANNER(state, status) {
    state.showBanner = status;
  },
  UPDATE_SHOWESSAY(state,status){
    state.showHomeEssay = status
  }
}

export default new Vuex.Store({
  state,
  mutations
})



