<template>
  <div class="welcome-container">
    <div class="welcome-img"></div>
    <div class="welcome-sentence">
      <div class="welcome-text">
        I can't change the direction of the wind, but I can adjust my sails to always reach my destination.      </div>
      <div class="shanbay-logo"></div>
    </div>
  </div>

</template>

<script>

    export default {
      name: "Welcome",
      created(){
        this.$store.commit("CHANGE_BANNER",false)
      },
      mounted() {
        setTimeout(() => {
          this.$router.push({path: '/login'})
        },2000)
      }
    }
</script>

<style lang="less" scoped>

  @keyframes welcomeImgAni
  {
    0%   {background-size: auto 100%;}
    100% {background-size: auto 103%;}
  }


  .welcome-container{
    position: fixed;
    top:0 ;
    bottom: 0;
    left: 0;
    right: 0;
      .welcome-img{
        width: 100%;
        height: 100%;
        background-image:url("https://b-ssl.duitang.com/uploads/item/201612/23/20161223112510_ktPTU.jpeg") ;
        background-repeat: repeat;
        animation:welcomeImgAni 2s linear;
        background-size: auto 103%;
      }
      .welcome-sentence{
        position: absolute;
        bottom: 0;
        height: 1.4rem;
        width: 100%;
        background-color: #fefbf4;
        .welcome-text{
          width: 96%;
          margin: 0.1rem auto;
          line-height: 0.30rem;
          font-size: 0.18rem;
          text-align: left;
          text-indent: 0.36rem;
          font-weight: lighter;
        }
        .shanbay-logo{
          width: 1rem;
          height: 0.5rem;
          background-image:url("https://static.baydn.com/static/img/logo_v5.png");
          background-repeat: no-repeat;
          background-size: contain;
          position: absolute;
          bottom: 0;
          right: 0;
          margin-right: 0.1rem;
        }
      }
    }

</style>



