<template>
  <div class="home-mine-container">
    <div class="iconfont mine-icon">&#xe611;</div>
  </div>
</template>

<script>
  export default {
    name: "homeMine",
    created() {
      if (false) {
        this.$router.push({
          name: 'Login'
        })
      }
      this.$store.commit("CHANGE_BANNER", true)
    }
  }
</script>

<style lang="less" scoped>
  .home-mine-container{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    /*background-color: #25a4bb;*/
    .mine-icon{
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      font-size: 1rem;
      color: #ccc;
    }
  }
</style>



