<template>
  <div>
    <div class="essay-detail-header">
      <div class="fl iconfont back-icon" @click="handleBackClick">&#xe64e;</div>
    </div>
    <div class="essay-detail-container">
      <div class="essay-detail-title">Alibaba's UCWeb to Launch an E-Commerce Service in India</div>
      <div class="essay-detail-source">来源：China Daily</div>
      <div class="essay-detail-labels">
        <div class="essay-detail-date fl">2019-01-11</div>
        <div class="essay-detail-lever fr">高考</div>
        <div class="essay-detail-wordnum fr">219词</div>
      </div>
      <div class="essay-detail-img">img</div>
      <div class="essay-detail-text" v-html="essayText">
      </div>
      <div class="essay-detail-finishread"><span>完成阅读</span></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "EssayDetail",
    data(){
      return{
        essayText:"UCWeb, a subsidiary of Chinese <span>giant</span> Alibaba Group, plans to <span>launch</span> an e-commerce service in India in the coming months.\n" +
          "\n" +
          "        In a statement to TechCrunch, a UCWeb spokesperson said the firm plans to build an e-commerce service around content platforms in India. It has no intention to compete with existing e-commerce businesses in the country, and Alibaba Group is not overseeing the development of the service, the spokesperson added.\n" +
          "\n" +
          "        UCWeb, which is best known for its <span>popular</span> mobile browser UC Browser, would leverage its “extensive user communities in India,” to build the e-commerce business, the spokesperson said. “The new service is in line with our strategy to enrich the experience for users and clients alike.”\n" +
          "\n" +
          "        UC Browser, used by more than 430 million users worldwide, counts India as one of its key markets where it has over 130 million users. According to third-party analytics firm StatCounter, UC Browser commanded over 23% of the mobile browser market share in India, lagging only behind Google Chrome, whose market share has ballooned in recent years to 63%.\n" +
          "\n" +
          "        UC Browser remains among the top 15 apps in India based on number of downloads through Google Play Store, according to app research firm SensorTower.\n" +
          "\n" +
          "        In recent years, UCWeb has bulked up UC Browser to expand its offering beyond mobile browsing. Today it works with over 120,000 bloggers and more than 700 media outlets to produce content that it then serves to UC Browser users.\n" +
          "\n" +
          "        UCWeb has launched a number of apps in recent quarters that are aimed at users who are trying to download videos from the web. Vmate, a UCWeb-owned app that offers similar functionalities, recently secured $100 million commitment from parent firm Alibaba.\n" +
          "\n" +
          "        On the sidelines of a company event on Thursday, Huaiyuan Yang, vice president of UCWeb Global Business, told news agency PTI that the firm would partner with existing players for its upcoming e-commerce service. Alibaba owns about 30% of payments and e-commerce firm Paytm.\n" +
          "\n" +
          "        “We have Alibaba’s e-commerce gene in us. We are actually trying to start an innovative business model related to e-commerce,” he told PTI.\n" +
          "      "
      }
    },
    methods: {
      handleBackClick() {
        this.$router.go(-1)
      }
    }
  }
</script>

<style lang="less" scoped>
  .fl {
    float: left;
  }
  .fr{
    float: right;
  }

  .essay-detail-header {
    z-index: 99;
    position: fixed;
    width: 100%;
    height: .5rem;
    line-height: .5rem;
    background-color: #fff;
    font-size: .36rem;
    padding: 0 0.2rem;

    .back-icon {
      font-size: .32rem;
      color: #333;
    }
  }

  .essay-detail-container {
    padding-top: .5rem;

    .essay-detail-title {
      padding: 0 .2rem;
      padding-top: .05rem;
      font-size: .25rem;
      line-height: .32rem;
      font-weight: bold;
    }
    .essay-detail-source{
      padding: 0 .2rem;
      margin-top: .1rem;
      line-height: .16rem;
      font-size: .14rem;
      color: #666;
    }
    .essay-detail-labels{
      padding: 0 .2rem;
      margin-top: .08rem;
      line-height: .16rem;
      font-size: .14rem;
      overflow:hidden;
      .essay-detail-date{
        color: #555;
      }
      .fr{
        margin-left: .06rem;
      }
    }
    .essay-detail-img{
      height: 2rem;
      width: 100%;
      background: #ee9900;
      margin-top: .2rem;
      background-image: url("http://x0.ifengimg.com/res/2019/4953111D65B63E0A5C8AF66BF27B0FD05AE8155E_size55_w550_h365.jpeg");
      background-size: cover;
      background-position: center center;
    }
    .essay-detail-text{
      padding: 0 .2rem;
      margin-top: .2rem;
      line-height: .24rem;
      font-size: .18rem;
      font-weight: lighter;
      color: #000;
      span{
        border-bottom: .01rem dashed red;
      }
    }
    .essay-detail-finishread{
      margin-top: .5rem;
      margin-bottom: .5rem;
      line-height: .4rem;
      height: .4rem;
      font-size: .18rem;
      background-color: #63a35c;
      border-radius: .4rem;
      color:#fff;
      width: 1.6rem;
      text-align: center;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }

  }
</style>



