<template>
  <div>
    <home-header></home-header>
    <div class="header-filed"></div>
    <short-essay v-show="showHomeEssay"></short-essay>
    <home-book v-show="!showHomeEssay"></home-book>
    <star-activ @closeActiveGuide="handleCloseActive" v-if="activGuideShow"></star-activ>
  </div>
</template>

<script>
  import HomeHeader from './homeIndexComps/homeHeader'
  import ShortEssay from './homeIndexComps/shortEssay'
  import HomeBook from './homeIndexComps/homeBook'
  import StarActiv from './StarActivGuide'
  import {mapState, mapMutations} from 'vuex'

  export default {
    name: "HomeIndex",
    components:{
      HomeHeader,
      ShortEssay,
      HomeBook,
      StarActiv
    },
    data() {
      return {
        activGuideShow:false
      }
    },
    computed:{
      ...mapState(['showHomeEssay'])
    },
    created() {
      if(false){
        this.$router.push({
          name:'Login'
        })
      }
      this.$store.commit("CHANGE_BANNER",true)
    },
    mounted(){
      if(localStorage.getItem("hasShowGuide") === "confirm"){
        this.activGuideShow = false
      }else{
        this.showActiveGuide()
      }
    },
    methods:{
      handleCloseActive(){
        this.activGuideShow=false
      },
      showActiveGuide(){
        this.activGuideShow = true
        localStorage.setItem("hasShowGuide","confirm")
      }
    }

  }
</script>

<style lang="less" scoped>
  .header-filed{
    height: .5rem;
  }
</style>



