<template>
  <router-link class="essay-item-container" to="/EssayDetail">
    <div class="essay-title">{{essayData.title}}</div>
    <div class="essay-info">
      <div class="essay-text-info">
        <div class="essay-text-top line-ellipsis">{{essayData.essayContent}}</div>
        <div class="essay-text-bottom">
          <span class="essay-label fl"><i class="iconfont label-icon">&#xe771;</i>{{essayData.essayLabel.l1}}</span>
          <span class="essay-label fl l2">{{essayData.essayLabel.l2}}</span>
          <span class="essay-label fr"><i class="iconfont label-icon">&#xe700;</i>{{essayData.essayLabel.l3}}</span>
          <span class="essay-label fr"><i class="iconfont label-icon">&#xe71b;</i>{{essayData.essayLabel.l4}}</span>
        </div>
      </div>
      <div class="essay-img" :style="{'background-image':'url('+essayData.backUrl+')'}"></div>
    </div>
  </router-link>
</template>

<script>
  export default {
    name: "shortEssayItem",
    props:{
      essayData:Object
    },
    data() {
      return {
      }
    }
  }
</script>

<style lang="less" scoped>
  .essay-item-container {
    border-bottom: .01rem solid #eee;
    width: 100%;

    .essay-title {
      margin: 0 auto;
      padding-top: .08rem;
      width: 95%;
      font-size: .17rem;
      line-height: .2rem;
      font-weight: bold;
      color: #333;
    }

    .essay-info {
      overflow: hidden;
      width: 95%;
      margin: 0 auto;
      margin-top: .08rem;
      padding-bottom: .05rem;

      .essay-text-info {
        width: 2.8rem;
        float: left;

        .essay-text-top {
          line-height: .22rem;
          font-size: .13rem;
          color: #999;

          &.line-ellipsis {
            height: .42rem;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
        }

        .essay-text-bottom {
          height: .3rem;
          .essay-label {
            margin-top: .07rem;
            font-size: .12rem;
            color: #555;
            height: .18rem;
            line-height: .18rem;
            .label-icon{
              font-size: .12rem;
              color: #555;
              padding: 0 .03rem;
              font-weight: bold;
            }
            &.fl {
              float: left;
              margin-right: .1rem;
            }
            &.fr {
              float: right;
              margin-left: .1rem;
            }
            &.l2{
              border: 1px solid #999;
              padding: 0 .06rem;
              border-radius: .1rem;
            }
          }
        }
      }

      .essay-img {
        width: 0.6rem;
        height: 0.6rem;
        float: right;
        border-radius: .05rem;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
      }
    }
  }
</style>



