<template>
  <div class="login-container">
    <div class="login-way">
      账号密码登录
    </div>
    <div class="shanbay-logo">
      <img src="https://media-image1.baydn.com/account/9ddc09ba7e97ff843e4ac98b1177fb595064c8a92cdea90352b1e93d73c045bc.png" alt="">
    </div>
    <div class="login-input-container">
      <div class="login-phonenum-wrap">
        <input class="login-phonenum-input" type="text" placeholder="手机号码" v-model="phoneNum">
      </div>
      <div class="login-verifcode-wrap">
        <input class="login-verifcode-input" type="text" placeholder="短信验证码" v-model="verifCode">
        <div  :class="{active:canGetVerif}" class="get-verifcode-btn" @click="handleGetVerif">{{verifReminder}}</div>
      </div>
    </div>
    <div class="login-btn-container">
      <div class="login-btn" :class="{active:submitStatus}" @click="handleSubmitMsg">快速登录/注册</div>
    </div>
    <div class="tips">温馨提示：未注册扇贝的手机号，登录时将自动注册</div>
    <div class="test">
      <div class="test-item" @click="enterIndex">测试:进入首页</div>
      <div class="test-item" @click="testShowIndexGuide">测试:点击-首页将展示活动引导card</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Login",
    data() {
      return {
        verifReminder: "获取验证码",
        phoneNum: '',
        verifTimer: 60,
        verifCode: ''       //用户填写的验证码
      }
    },
    created(){
      this.$store.commit("CHANGE_BANNER",false)
    },
    methods: {
      handleGetVerif() {
        if (this.canGetVerif) {
          this.$toast('验证码已发送');
          this.verifTimer--;
          console.log(this.phoneNum)
          this.submitPhoneNum(this.phoneNum);
          this.verifReminder = "已发送" + this.verifTimer + "s";
          let intervalId = setInterval(() => {
            //定时不断减小 ( 60s -> 0s )
            this.verifTimer--;
            this.verifReminder = '已发送' + this.verifTimer + 's'
            //倒计时到0,恢复原内容和功能
            if (this.verifTimer === 0) {
              clearInterval(intervalId);  //清除定时器
              this.verifReminder = '获取验证码'
              this.verifTimer = 60;
            }
          }, 1000)
        }
      },
      submitPhoneNum() {
        //向后端发送手机号码
        console.log(this.phoneNum + "获取验证码")
      },
      handleSubmitMsg() {
        if (this.submitStatus) {
          this.$toast('正在登录');
          //向后端提交手机号码和验证码
          console.log("提交手机号" + this.phoneNum + "提交验证码" + this.verifCode)
          setTimeout(() => {this.$router.push({name:'HomeIndex'})},1000) //模拟成功登录
        }
      },
      checkPhone(phone) {
        var pattern = /^1[3,4,5,7,8,9][0-9]{9}$/;
        return pattern.test(this.phoneNum.toString())
      },
      enterIndex(){
        this.$router.push({name:'HomeIndex'})
      },
      testShowIndexGuide(){
        localStorage.removeItem("hasShowGuide")
        this.$toast("已清除localStorage,进入首页将显示 首页引导")
      }
    },
    computed: {
      canGetVerif() {
        if (this.checkPhone(this.phoneNum) && this.verifTimer === 60) {
          return 1
        } else {
          return 0
        }
      },
      submitStatus() {
        if (this.checkPhone(this.phoneNum) && this.verifCode) {
          return 1;
        } else {
          return 0
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .login-container {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    /*background: #25a4bb;*/
    .login-way{
      text-align: right;
      padding-right: .2rem;
      font-size: .14rem;
      color:#209e85;
      height: .3rem;
      line-height: .3rem;
      margin-top: .05rem;
    }
    .shanbay-logo{
      margin-top: .5rem;
      img{
        display: block;
        margin: 0 auto;
        width: 1.3rem;
      }
    }
    .login-input-container{
      margin-top: .7rem;
      padding: 0 .2rem;
      .login-phonenum-wrap{
        width: 100%;
        height: .3rem;
        line-height: .3rem;
        .login-phonenum-input{
          color: #666;
          font-size: .16rem;
          width: 100%;
          border-bottom: .01rem solid #ccc;
        }
      }
      .login-verifcode-wrap{
        clear: both;
        margin-top: .5rem;
        width: 100%;
        height: .5rem;
        .login-verifcode-input{
          color: #666;
          font-size: .16rem;
          float: left;
          border-bottom: .01rem solid #ccc;
          padding-top: .18rem;
        }
        .get-verifcode-btn{
          padding: 0 .2rem;
          line-height: .4rem;
          height: .4rem;
          font-size: .16rem;
          float: right;
          border: .01rem solid #ccc;
          color: #999;
        }
        .active{
          color: #209e85;
          border: .01rem solid #209e85;
        }
      }
    }
    .login-btn-container{
      margin-top: .8rem;
      padding: 0 0.2rem;
      .login-btn{
        width: 100%;
        height: .38rem;
        line-height: .38rem;
        text-align: center;
        background-color: #eee;
        color: #bebebe;
        font-size: .16rem;
      }
      .active{
        background-color: #209e85;
        color: #fff;
      }
    }
    .tips{
      font-size: .12rem;
      margin-top: .08rem;
      text-align: center;
      color: #999;
    }
    .test{
      width: 60%;
      position: absolute;
      font-size: .14rem;
      bottom: .3rem;
      left: 50%;
      transform: translate(-50%,0);
      color: #209e85;
      .test-item{
        margin-bottom: .1rem;
      }
    }

  }

</style>



