<template>
  <div class="star-activ-container">
    <div class="star-activ-wrap">
      <router-link class="activ-img" tag="div" to='/homeFind'></router-link>
      <div class="close-btn iconfont" @click="handleCloseClick">&#xe711;</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "StarActiv",
    methods:{
      handleCloseClick(){
        this.$emit('closeActiveGuide')
      }
    }
  }
</script>

<style lang="less" scoped>
  .star-activ-container {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 999;

    .star-activ-wrap {
      width: 2.8rem;
      height: 4.1rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      .activ-img {
        height: 3.5rem;
        background: #fff;
        background-image: url("../../../assets/image/activity.png");
        background-repeat: no-repeat;
        background-size: contain;
        border-radius: .1rem;
      }

      .close-btn {
        text-align: center;
        font-size: .25rem;
        margin-top: .3rem;
        color: #fff;
      }
    }

  }
</style>



