<template>
  <div
    v-infinite-scroll="loadMore"
    infinite-scroll-immediate-check=true
    :infinite-scroll-disabled="busy"
    infinite-scroll-distance="20"
  >
    <short-essay-item
      v-for="essayData of essayList"
      :essayData="essayData"
    ></short-essay-item>
  </div>
</template>

<script>
  import axios from 'axios'
  import ShortEssayItem from './shortEssayItem'

  export default {
    name: "shortEssay",
    components: {
      ShortEssayItem
    },
    data(){
      return{
        essayList:[],
        busy:true
      }
    },
    methods:{
      loadMore(){
        console.log('load');
        this.$toast('加载中',1000);
        this.busy=false;
        axios.get('./static/mock/essayList.json').then((res) => {
          this.essayList = this.essayList.concat(res.data.data.essayList)
          this.busy=true
        })
      }
    },
    mounted() {
      this.loadMore();
    }
  }
</script>

<style scoped>

</style>



