import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Welcome from '@/components/welcome/Welcome'
import Login from '@/components/login/Login'
import HomeIndex from '@/components/home/homeIndex/HomeIndex'
import HomeLesson from '@/components/home/homeLesson/HomeLesson'
import HomeFind from '@/components/home/homeFind/HomeFind'
import HomeMine from '@/components/home/homeMine/HomeMine'
import EssayDetail from '@/components/detail/EssayDetail'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
     redirect:'/welcome'
    },
    {
      path: '/welcome',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/homeIndex',
      name: 'HomeIndex',
      component: HomeIndex
    },
    {
      path: '/homeLesson',
      name: 'HomeLesson',
      component: HomeLesson
    },
    {
      path: '/homeFind',
      name: 'HomeFind',
      component: HomeFind
    },
    {
      path: '/homeMine',
      name: 'HomeMine',
      component: HomeMine
    },
    {
      path: '/essayDetail',
      name: 'EssayDetail',
      component: EssayDetail
    }
  ]
})



