<template>
  <div class="home-find-container">
    <div class="iconfont find-icon">&#xe60d;</div>
  </div>
</template>

<script>
  export default {
    name: "HomeFind",
    data() {
      return {}
    },
    created() {
      if(false){
        this.$router.push({
          name:'Login'
        })
      }
      this.$store.commit("CHANGE_BANNER",true)
    }

  }
</script>

<style lang="less" scoped>
  .home-find-container{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  /*background-color: #25a4bb;*/
  .find-icon{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    font-size: 1rem;
    color: #ccc;
  }
  }
</style>



